
import { Options, Vue } from "vue-class-component";
import { ChatModule } from "@/store/modules/chat/chat";
@Options({
  name: "ChatCell",
  props: {
    name: { type: String },
    img: { type: String },
    message: { type: String },
    date: { type: String },
    id: { type: Number },
  },
})
export default class extends Vue {
  nav() {
    // console.log(this.$props);
    this.$router.push("/chat");
    ChatModule.setHead({
      name: this.$props["name"],
      img: this.$props["img"],
      id: this.$props["id"],
    });
  }
}
